.loader__wrapper {
  display: flex;
  justify-content: center;
  margin-top: 50px;
  align-items: center;
}

.loader {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border: 3px dashed teal;
  animation: rotate 1s infinite linear;
}

@keyframes rotate {
  from {
    transform: rotate(0deg) scale(1);
  }
  to {
    transform: rotate(360deg) scale(1.4);
  }
}
